@mixin price {
  @include font-text;
  font-size: 19.36px;
  color: rgb(51, 51, 51);
  line-height: 1;
  width: fit-content;
  margin: 0;
}

@mixin heading {
  z-index: 3;
  width: fit-content;
  position: relative;
  font-size: 88px;

  &::before {
    position: absolute;
    right: 0;
    bottom: -20px;
    content: "";
    height: 4px;
    width: calc(100% + 175px);
    background-color: $red-color;
  }
}

@mixin headingSmall {
  font-size: 72px;
  margin-left: 100px;
}

@mixin headingMore {
  position: absolute;
  font-size: 24px;
  line-height: 36px;
  font-weight: 400;
  width: fit-content;
  color: $red-color;
  bottom: 0;
  left: calc(100% + 80px);

  &::before {
    position: absolute;
    left: -45px;
    bottom: 16px;
    content: "";
    width: 37px;
    height: 2px;
    background-color: $red-color;
    transition: transform 0.5s;
  }

  &::after {
    position: absolute;
    right: 0;
    opacity: 0;
    bottom: 13px;
    content: "";
    width: 36px;
    height: 10px;
    background-image: url("../images/icons/details.svg");
    background-position: center;
    transition: transform 1s, opacity 1s;
  }

  &:hover {
    &::before {
      transform: rotate(180deg);
    }

    &::after {
      opacity: 100;
      transform: translateX(45px);
    }
  }
}

@mixin headingMobil {
  font-size: 48px;
  margin-left: 0;
}

@mixin headingMobilSmall {
  font-size: 40px;
}

@mixin headingMoreMobil {
  left: 0;
  top: 150%;

  &::before {
    top: 16px;
  }

  &::after {
    top: 13px;
  }
}

@mixin headingMobileHorizontal {
  position: sticky;
  font-size: 40px;
  top: 40px;
  width: 100vw;
  line-height: 70px;
  margin-bottom: 0;
  background-color: $bg-color;

  &::before {
    bottom: 0;
  }
}

@mixin headingHorizontalBlock {
  &__heading {
    font-size: 40px;
  }

  &__more {
    font-size: 18px;
  }

  &__headingBlock {
    z-index: 1;
    position: sticky;
    top: 60px;
  }
}
